body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-urbanist: Urbanist;

  /* font sizes */
  --font-size-xs: 12px;
  --font-size-xl: 20px;
  --font-size-mini: 15px;
  --font-size-11xl: 30px;

  /* Colors */
  --color-white: #fff;
  --color-darkslategray: #434744;
  --color-orange: #f0a202;
  --color-salmon: #ed6a5e;

  /* border radiuses */
  --br-xl: 20px;
}
